:root {
  --color-button-secondary-background: #FFFFFF;
  --color-button-secondary-text: #222222;
  --color-button-primary-background: #008A00;
  --color-button-primary-text: #FFFFFF;
  --color-field-background: #FFFFFF;
  --color-field-border: #CED3D9;
  --color-field-text: #222222;
  --color-form-background: #FFFFFF;
  --color-form-text: #4F5861;
  --color-link-primary: #0071C4;
  --color-message-error: #DA273B;
  --color-message-warning: orange;
  --color-page-background: #F6F7F8;
  --color-page-text: #222222;
  --border-radius-form: 4px;
  --border-radius-field: 3px;
  --box-shadow-form: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
  --sizing-app-brand-height: 40px;
  --sizing-content-width: 330px;
  --spacing-app-brand: 30px;
  --spacing-field-content: 8px;
  --spacing-form: 30px 20px 40px;
  --spacing-form-desktop: 30px 30px 40px;
  --spacing-form-controls: 20px;
  --spacing-form-label-bottom: 5px;
  --spacing-silverstripe-brand: 30px;
  --spacing-submit-content: 10px 20px;
}

@media (prefers-color-scheme: dark) {
  :root {
    --color-button-secondary-background: #3E464E;
    --color-button-secondary-text: #FFFFFE;
    --color-button-primary-background: #F6C445;
    --color-button-primary-text: #3E464E;
    --color-field-background: #4F5861;
    --color-field-border: #4F5861;
    --color-field-text: #FFFFFE;
    --color-form-background: #3E464E;
    --color-form-text: #F7F8FA;
    --color-link-primary: #74D6F8;
    --color-page-background: #4F5861;
    --color-page-text: #FFFFFE;
  }
}

html {
  font: 14px/1.5 sans-serif;
}

a {
  color: var(--color-link-primary);
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

a.link--secondary {
  color: var(--color-form-text);
}

label {
  font-size: 0.9285714286rem;
}

input {
  font-size: 1rem;
}

* {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
}

body {
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  grid-template-rows: 1fr calc(var(--sizing-app-brand-height) + (var(--spacing-app-brand) * 2)) auto 80px 1fr;
  height: 100vh;
}

@media (min-width: 420px) {
  body {
    grid-template-columns: 1fr max-content 1fr;
  }
}

.app-brand {
  grid-column: 2;
  grid-row: 2;
}

.login-form {
  grid-column: 2;
  grid-row: 3;
  display: block;
}

.silverstripe-brand {
  grid-column: 2;
  grid-row: 4;
}

.app-brand {
  display: flex;
  justify-content: center;
  padding: var(--spacing-app-brand);
}

.app-brand__link {
  color: inherit;
  text-decoration: none;
}

.app-brand__link:hover {
  text-decoration: none;
}

.app-brand__name {
  margin: 0;
}

.app-brand__logo + .app-brand__name {
  display: none;
}

.app-brand__logo--dark-mode {
  display: none;
}

@media (prefers-color-scheme: dark) {
  .app-brand__logo--light-mode {
    display: none;
  }

  .app-brand__logo--dark-mode {
    display: block;
  }
}

.silverstripe-brand {
  color: var(--color-page-text);
  display: flex;
  justify-content: center;
  padding: var(--spacing-silverstripe-brand);
}

html {
  background-color: var(--color-page-background);
  color: var(--color-page-text);
}

.login-form {
  background-color: var(--color-form-background);
  box-shadow: var(--box-shadow-form);
  color: var(--color-form-text);
  padding: var(--spacing-form);
  width: 100vw;
}

@media (min-width: 420px) {
  .login-form {
    border-radius: var(--border-radius-form);
    padding: var(--spacing-form-desktop);
    width: auto;
  }
}

.login-form__title {
  margin-top: 0;
}

.login-form__content,
.login-form__message,
.message {
  max-width: var(--sizing-content-width);
}

.login-form__message--warning,
.message.warning {
  color: var(--color-message-warning);
}

.login-form__message--error,
.message.bad,
.message.required {
  color: var(--color-message-error);
}

.login-form__fields,
fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

.login-form-control,
.field {
  margin-bottom: var(--spacing-form-controls);
}

.login-form-control--text .login-form-control__label,
.field.text label {
  display: block;
  margin-bottom: var(--spacing-form-label-bottom);
}

.login-form-control--text .login-form-control__field,
input.text {
  background-color: var(--color-field-background);
  border: 1px solid var(--color-field-border);
  border-radius: var(--border-radius-field);
  color: var(--color-field-text);
  padding: var(--spacing-field-content);
  width: 100%;
}

@media (min-width: 420px) {
  .login-form-control--text .login-form-control__field,
  input.text {
    width: var(--sizing-content-width);
  }
}

.login-form-actions,
.btn-toolbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.btn,
.action {
  background-color: var(--color-button-secondary-background);
  border: 1px solid currentColor;
  border-radius: var(--border-radius-field);
  color: var(--color-button-secondary-text);
  cursor: pointer;
  padding: var(--spacing-submit-content);
}

.btn-success,
input[type="submit"] {
  background-color: var(--color-button-primary-background);
  color: var(--color-button-primary-text);
  border: 0;
}




